@use "./../../foundation/color" as *;

.p-home-menu {
  height: 350px;
  padding-top: 50px;

  &__lists {
    display: flex;
    justify-content: space-evenly;
  }

  &__list {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 250px;
    height: 250px;
    // border: 1px solid #000000;
    // box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25), 0px 4px 4px rgba(0, 0, 0, 0.25);
    position: relative;
    z-index: 0;

    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 250px;
      height: 250px;
      background: rgba(0, 0, 0, 0.5);
      z-index: 0;
    }

    &--salon {
      background-image: url(../img/リンクsalon.png);
    }
    &--menu {
      background-image: url(../img/リンクmenu.png);
    }
    &--notes {
      background-image: url(../img/リンクnotes.png);
    }
  }
  &__link {
  }
  &__text {
    display: inline-block;
    font-size: 48px;
    color: $c-main;
    z-index: 1;
  }
}
